@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap" ); 


:root{
    --main-color:#d3ad7f;
    --black: #13131a;
    --bg:#010103;
    --border:.1rem solid rgba(255,255,255,.3) ;
}
*{
    font-family: 'Roboto',sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    border: none;
    text-decoration: none;
    text-transform:capitalize;
    transition: 0.5s linear;
}



body{
    background-color: var(--bg);
   
}

section{
    padding: 2rem 7%;
}

.heading{
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    padding-bottom: 3.5rem;
    font-size: 4rem;
}

.heading span{
    color: var(--main-color);
    text-transform: uppercase;
}

.btn{
    margin-top: 0.5rem;
    display: inline-block;
    padding: .9rem 3rem;
    font-size: 1.7rem;
    color: #fff;
    background: var(--main-color);
    cursor: pointer;
}

.btn:hover{
    letter-spacing: .2rem;
}

.header{
    background: var(--bg);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5rem 7%;
    border-bottom: var(--border);
    position: fixed;
    top: 0; left: 0; right: 0;
    z-index: 1000;
}

.header .logo img{
     height: 5rem;
}

.header .navbar a{
    margin: 0 1rem;
    font-size: 1.6rem;
    color: #fff;
}

.header .navbar a:hover{
    color: var(--main-color);
    border-bottom: .1rem solid var(--main-color);
    padding-bottom: 0.5rem;
}

.header .icons div{
    color: #fff;
    cursor: pointer;
    font-size: 1.6rem;
    margin-left: 2rem;
}



.header .icons div:hover{
    color: var(--main-color);
}

#menu-btn{
    display:none ;
}

.header .search-form{
    position: absolute;
    top: 115%;
    right: 7%;
    background: #fff;
    width: 50rem;
    display: flex;
    align-items: center;
    transform: scaleY(0);
    transform-origin:top ;
}

.header .search-form .active{
    transform: scaleY(1);
}

.header .search-form input{
    width: 100%;
    height: 100%;
    font-size: 0.6rem;
    color: var(--black);
    padding: 1rem;
    text-transform: none;
}

.header .search-form label{
    cursor: pointer;
    font-size: 2.2rem;
    margin-right: 1.5rem;
    color: var(--black);
}

.header.search-form label:hover{
    color: var(--main-color);
}

.header .cart-items-container{
    position: absolute;
    top: 100%; right: -100%;
    height: calc(100vh-9.5rem);
    width: 35rem;
    background: #fff;
    padding: 0 1.5rem;
   
}

.header .cart-items-container .active{
     right: 0;
}


.header .cart-items-container .cart-items{
    position: relative;
    margin: 2rem 0;
    display: flex;
    align-items: center;
    gap: 1.5rem;
}

.header .cart-items-container .cart-items .fa-times{
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 2rem;
    cursor: pointer;
    color: var(--black);
}

.header .cart-items-container .cart-items .fa-timesL:hover{
    color: var(--main-color);

}

.header .cart-items-container  .cart-items img{
    height: 7rem;

}

.header .cart-items-container  .cart-items .content h3{
   font-size: 2rem;
   color: var(--black);
   padding-bottom: .5rem;
}

.header .cart-items-container  .cart-items .content .price{
    font-size: 1.5rem;
    color: var(--main-color);
 }

 .header .cart-items-container .btn{
    width: 100%;
    text-align: center;
 }

 .home{
    background: url(trial4.mp4);
    min-height: 100vh;
    display: flex;
    align-items: center;
    
    justify-content: center;
    background-size: cover;
    background-repeat: no-repeat;
  
 }
.home::before{
    z-index: 777;
    content: '';
    position: absolute;
   
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
 .home.video{
    z-index: 000;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
 }

 .home .content{
    max-width: 60rem;
 }

 .home .content h3{
    font-size: 6rem;
    text-transform: uppercase;
    color: #fff;
 }


 .home .content p{
    font-size: 2rem;
    font-weight: lighter;
    line-height: 1.8;
    padding: 1rem 0;
    color: #eee;
 }

.about .row{
    display: flex;
    align-items: center;
    background: var(--black);
    flex-wrap: wrap;
}

.about .row .image{
    flex: 1  1 20rem;
}

.about .row .image img{
    width: 100%;
}

.about .row .content{
    flex: 1 1 45rem;
    padding: 2rem;
}

.about .row .content h3{
    font-size: 3rem;
    color: #fff;

}

.about .row .content p{
    font-size: 1.6rem;
    color: #ccc;
    padding: 1rem 0;
    line-height: 1.8;
    
}

.menu .box-container{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(20rem,1fr));
    gap: 1.5rem;
}

.menu .box-container .box{
    padding: 5rem;
    text-align: center;
    border: var(--border);


}

.menu .box-container .box img{
    height: 10rem;

}

.menu .box-container .box h3{
    color: #fff;
    font-size: 2rem;
    padding: 1rem 0;
}



.menu .box-container .box .price{
    color: #fff;
    font-size: 2.5rem;
    padding: .5rem 0;
}

.menu .box-container .box .price span{
    font-size: 1.5rem;
    text-decoration: line-through;
    color: var(--main-color);
}

.menu .box-container .box:hover{
    background: #fff;

}

.menu .box-container .box:hover > *{
    color: var(--black);
}

.products .box-container{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(10rem,1fr));
    gap: 1.5rem;
}


.products .box-container .box{
    text-align: center;
    border: var(--border);
    padding: 2rem;

}

.products .box-container .box .icons a{
    height: 3rem;
    width: 3rem;
    line-height: 3rem;
    font-size: 2rem;
    border: var(--border);
    color: #fff;
    margin: .3rem;
}

.products .box-container .box .icons a:hover{
    background-color: var(--main-color);
}

.products .box-container .box  .image{
    padding: 2.5rem 0;
}

.products .box-container .box .image img{
    height: 15rem;
}

.products .box-container .box  .content h3{
    color: #fff;
    font-size: 2.5rem;
}

.products .box-container .box  .content .stars{
    padding: 1.5rem;
}

.products .box-container .box .content .stars i{
    font-size: 1.7rem;
    color: var(--main-color);
}

.products .box-container .box .content .price{
    color: #fff;
    font-size: 2.5rem;
}

.products .box-container .box .content .price span{
    color: var(--main-color);
    text-decoration: line-through;
    font-weight: lighter;
    font-size: 1.5rem;
}

.review .box-container{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(10rem,1fr));
    gap: 1.5rem;
}


.review .box-container .box{
    text-align: center;
    border: var(--border);
    padding: 3rem 2rem;

}

.review .box-container .box p{
    color: #fff;
    font-size: 1.5rem;
    line-height: 1.8rem;
    padding: 2rem 0;
}

.review .box-container .box .user{
    height: 7rem;
    width: 7rem;
    border-radius: 50%;
    object-fit: cover;
}

.review .box-container .box h3{
    color: #fff;
    font-size: 2rem;
    padding: 1rem 0;
}

.review .box-container .box i{
    color: var(--main-color);
    font-size: 1.5rem;
}

.contact .row{
    display: flex;

    background: var(--black);
    flex-wrap: wrap;
    gap: 1rem;
}

.contact .row .map{
    flex: 1 1 45rem;
    width: 100%;
    object-fit: cover;
}

.contact .row form{
   
   padding: 5rem 2rem;
}

.contact .row form h3{
    text-transform: uppercase;
    font-size: 3.5rem;
    color: #fff;
}

.contact .row form .inputbox{
    display: flex;
    align-items: center;
    margin-top: 2rem;
    margin-bottom: 2rem;
    background: var(--black);
    border: var(--border );
}

.contact .row form .inputbox span{
    color: #fff;
    font-size: 2rem;
    padding-left: 2rem;
}

.contact .row form .inputbox input{
    width: 100%;
     padding: 1rem;
    font-size: 1.7rem;
    color: #fff;
    text-transform: none;
    background: none;

}

.blogs .box-container{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(10rem,1fr));
    gap: 1.5rem;
}

.blogs .box-container .box{
    border: var(--border);
}

.blogs .box-container .box .image{
    height: 25rem;
    overflow:hidden ;
    width: 100%;
}

.blogs .box-container .box .image img{
    height: 100%;
    object-fit: cover;
    width: 100%;
}

.blogs .box-container .box:hover .image img{
    transform: scale(1.2);
}

.blogs .box-container .box .content{
    padding: 2rem;
}

.blogs .box-container .box .content .title{
    font-size: 2.5rem;
    line-height: 2.5rem;
    color: #fff;
}


.blogs .box-container .box .content .title:hover{
    color: var(--main-color);
}

.blogs .box-container .box .content span{
    color: var(--main-color);
    display: block;
    padding-top: 1rem;
    font-size: 1.5rem;
}

.blogs .box-container .box .content p{
    font-size: 1rem;
    color: #ccc;
    line-height: 1.8;
    padding: 1rem 0;
}

/* scrollbar ki css */
::-webkit-scrollbar{
    width: 0;
}
#scrollPath{
    position: fixed;
    top: 0;
    right: 0;
    width: 10px;
    height: 100%;
    background: rgba(255,255,255,.05);
}

#progressbar{
    position: fixed;
    top: 0;
    right: 0;
    width: 10px;
    height: 100%;
    background: linear-gradient(to top,#008aff,#00ffe7);
    animation: animate 5s linear infinite;
}

@keyframes animate
{
    0%,100%
    {
        filter: hue-rotate(0deg);
    }
    50%
    {
        filter: hue-rotate(360deg);
    }
}
#progressbar::before
{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to top,#008aff,#00ffe7);
    filter: blur(10px);
}

#progressbar::after
{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    /* height: 100%; */
    background: linear-gradient(to top,#008aff,#00ffe7);
    filter: blur(30px);
}

/* smiley ki css */
.container
{

position: relative;
display: flex;
justify-content: center;
align-items: center;
height: 70vh;
width: 100%;
cursor: url(coffee.svg),pointer;
}

.container .face
{
position: relative;
width: 300px; 
height: 300px; 
border-radius: 50%; 
/* background: #ffcd00; */
background: linear-gradient(180deg,#f44336,#ffcd00);
display: flex;
justify-content: center;
align-items: center;
margin: 0 20px;
}

.container .face:hover {
    /* background: linear-gradient(180deg,#f44336,#ffcd00); */
    background: #ffcd00;
   
}
/* smile */
.container .face:before{
    content: '';
    position: absolute;
    top: 210px;
    width: 150px;
    /* height: 70px; */
    height: 20px;
    border-bottom-left-radius: 70px;
    border-bottom-right-radius: 70px;
    background: #b57700;
    transition: 0.5s;
}

.container .face:hover:before{
    top: 210px;
    width: 150px;
    /* height: 20px; */
    height: 70px;
    border-bottom-left-radius: 70px;
    border-bottom-right-radius: 70px;
    background: #9d0b00;
}

.container .face .eyes
{
    position: relative;
    top: 0;
    display: flex;
    flex-wrap: wrap;
}

.container .face .eyes .eye{
    position: relative;
    width: 80px;
    height: 80px;
    display: block;
    margin: 0 15px;
    background: #fff;
    border-radius: 50%;
}

.container .face .eyes .eye::before{
    content: '';
    position: absolute;
    top: 50%;
    left: 25px;
    transform: translate(-50%,-50%);
    width: 40px;
    height: 40px;
    background: #333;
    border-radius: 50%;
}

.container h2{
    position: absolute;
    top: 75px;
    font-size: 3rem;
    color: var(--main-color);
}

.container h2:last-child{
    position: absolute;
    top: initial;
    bottom: 75px;
    font-size: 3rem;
    color: #fff;

}
/* collage ki css */
.list {
    
    columns: 300px;
    gap: 10px;
  }
  
  .list img {
    width: 100%; 
    height: auto;
    border-radius: 8px;
    object-fit: cover; 
  }



















 @media(max-width:991px){
    .header{
        padding: 1.5rem 2rem;
    }

    section{
        padding: 2rem ;
    }
 }


 @media(max-width:768px){
    #menu-btn{
        display: inline-block;
    }

    .header .navbar{
        position: absolute;
        top:100%; right: -100%;
        width: 30rem;
        height: calc(100vh-9.5rem);

    }

    .header .navbar .active{
        right: 0;

    }

    .header .navbar a{
        color: var(--black);
        display: block;
        margin: 1.5rem;
        padding: .5rem;
        font-size:2rem;
    }

    .header .search-form{
        width: 90%;
        right: 2rem;
    }

    .home{
        background-position: left;
        justify-content: center;
        text-align: center;
    }

    .home .content h3{
        font-size: 4.5rem;
    }

    .home .content p{
        font-size: 1.5rem;
    }
 }


 @media(max-width:450px){
    .header{
        padding: 1.5rem 2rem;
    }
 }

